import flags from "./flags";
import { patch } from "./patch";
const { VNodeFlags, ChildrenFlags } = flags;
import { createTextVNode } from './h'

export function render(vnode, container) {
  const prevVNode = container.vnode;
  if (prevVNode == null) {
    if (vnode) {
      // Ê≤°ÊúâÊóßÁöÑ VNodeÔºåÂè™ÊúâÊñ∞ÁöÑ VNode„ÄÇ‰ΩøÁî® `mount` ÂáΩÊï∞ÊåÇËΩΩÂÖ®Êñ∞ÁöÑ VNode
      mount(vnode, container);
      // Â∞ÜÊñ∞ÁöÑ VNode Ê∑ªÂä†Âà∞ container.vnode Â±ûÊÄß‰∏ãÔºåËøôÊ†∑‰∏ã‰∏ÄÊ¨°Ê∏≤ÊüìÊó∂ÊóßÁöÑ VNodeÂ∞±Â≠òÂú®‰∫Ü
      container.vnode = vnode;
    }
  } else {
    if (vnode) {
      // ÊúâÊóßÁöÑ VNodeÔºå‰πüÊúâÊñ∞ÁöÑ VNode„ÄÇÂàôË∞ÉÁî® `patch` ÂáΩÊï∞ÊâìË°•‰∏Å
      patch(prevVNode, vnode, container);
      // Êõ¥Êñ∞ container.vnode
      container.vnode = vnode;
    } else {
      // ÊúâÊóßÁöÑ VNode ‰ΩÜÊòØÊ≤°ÊúâÊñ∞ÁöÑ VNodeÔºåËøôËØ¥ÊòéÂ∫îËØ•ÁßªÈô§ DOM,Âú®ÊµèËßàÂô®‰∏≠ÂèØ‰ª•‰ΩøÁî® removeChildÂáΩÊï∞„ÄÇ
      container.removeChild(prevVNode.el);
      container.vnode = null;
    }
  }
}

function mount(vnode, container, isSVG) {
  const { flags } = vnode;
  if (flags & VNodeFlags.ELEMENT_HTML) {
    // ÊåÇËΩΩÊôÆÈÄöÊ†áÁ≠æ
    mountElement(vnode, container, isSVG);
  } else if (flags & VNodeFlags.COMPONENT) {
    // ÊåÇËΩΩÁªÑ‰ª∂
    mountComponent(vnode, container, isSVG);
  } else if (flags & VNodeFlags.TEXT) {
    // ÊåÇËΩΩÂ≠òÊñáÊú¨
    mountText(vnode, container);
  } else if (flags & VNodeFlags.FRAGMENT) {
    // ÊåÇËΩΩFragment
    mountFragment(vnode, container, isSVG);
  } else if (flags & VNodeFlags.PORTAL) {
    // ÊåÇËΩΩ Portal
    mountPortal(vnode, container, isSVG);
  }
}

// Â§ÑÁêÜVNodeData‰∏≠Èô§ class Âíå style ‰πãÂ§ñÁöÑÂÖ®ÈÉ®Êï∞ÊçÆÔºåÂΩìÁÑ∂‰πüË¶ÅÊéíÈô§ VNodeData‰∏≠ÁöÑtargetÂ±ûÊÄßÔºåÂõ†‰∏∫ÂÆÉÂè™ÈÄÇÁî®‰∫é Portal
const domPropsRE = /\[A-Z]|^(?:value|checked|slected|muted)$/;

function mountElement(vnode, container, isSVG) {
  // Ê≥®ÔºöËøêÁÆóÁ¨¶‰ºòÂÖàÁ∫ß  ÈÄªËæëÊàñ 5 (||)‰ªéÂ∑¶Âà∞Âè≥  Êåâ‰Ωç‰∏é 9(&) ‰ªéÂ∑¶Âà∞Âè≥

  // 4„ÄÅÂ§ÑÁêÜSVGÊ†áÁ≠æ
  isSVG = isSVG || vnode.flags & VNodeFlags.ELEMENT_SVG;
  const el = isSVG
    ? document.createElementNS("http://www.w3.org/2000/svg", vnode.tag)
    : document.createElement(vnode.tag);
  vnode.el = el; // 1„ÄÅVNodeÊ∏≤ÊüìÊàêÁúüÂÆûDOM‰πãÂêéÔºåÂºïÁî®ÁúüÂÆûDOM
  // ÊãøÂà∞ VNodeData
  const data = vnode.data;
  if (data) {
    // Â¶ÇÊûúdataÂ≠òÂú®ÔºåÂàôÈÅçÂéÜ   2„ÄÅÂ∞ÜVNodeDataÂ∫îÁî®Âà∞ÁúüÂÆûDOMÂÖÉÁ¥†‰∏ä
    for (let key in data) {
      // keyÂèØËÉΩÊòØclass„ÄÅstyle„ÄÅonÁ≠âÁ≠â
      switch (key) {
        case "style":
          // Â¶ÇÊûú key ÁöÑÂÄºÊòØ styleÔºåËØ¥ÊòéÊòØÂÜÖËÅîÊ†∑ÂºèÔºåÈÄê‰∏™Â∞ÜÊ†∑ÂºèËßÑÂàôÂ∫îÁî®Âà∞ el
          for (let k in data.style) {
            el.style[k] = data.style[k];
          }
          break;
        case "class":
          el.className = dynamicClass(data[key]);
          break;
        default:
          // ‰∫ã‰ª∂Â§ÑÁêÜ
          if (key[0] === "o" && key[1] === "n") {
            el.addEventListener(key.slice(2), data[key]);
            // Ê≥®ÔºöÂ¶ÇÊ≠§‰∏ÄÊù•ÔºåÊâÄÊúâ‰ª• 'on' ÂºÄÂ§¥ÁöÑÂ±ûÊÄßÈÉΩË¢´Âà§ÂÆö‰∏∫‰∫ã‰ª∂
          } else if (domPropsRE.test(key)) {
            // ÂΩìÂÅö DOM PropÂ§ÑÁêÜ
            el[key] = data[key];
          } else {
            // ÂΩì‰Ωú Attr Â§ÑÁêÜ
            el.setAttribute(key, data[key]);
          }
          break;
      }
    }
  }

  // 3„ÄÅÁªßÁª≠ÊåÇËΩΩÂ≠êËäÇÁÇπ
  // ÊãøÂà∞children Âíå childFlags
  const childFlags = vnode.childFlags;
  const children = vnode.children;
  // Ê£ÄÊµãÂ¶ÇÊûúÊ≤°ÊúâÂ≠êËäÇÁÇπÂàôÊó†ÈúÄÈÄíÂΩíÊåÇËΩΩ
  if (childFlags !== ChildrenFlags.NO_CHILDREN) {
    if (childFlags & ChildrenFlags.SINGLE_VNODE) {
      // Âçï‰∏™Â≠êËäÇÁÇπÂàôË∞ÉÁî®mountÊåÇËΩΩ
      mount(children, el, isSVG); // ÊääisSvg‰º†ÈÄí‰∏ãÂéªÔºåÊñπ‰æøÂêéÁª≠Âà§Êñ≠Ê∏≤ÊüìsvgÈáåÈù¢circleÁ≠âÂÖÉÁ¥†tag‰∏çÁ≠â‰∫ésvgÊó∂Ê∏≤ÊüìsvgÊ†áÁ≠æ
    } else if (childFlags & ChildrenFlags.MULTIPLE_VNODES) {
      // Â¶ÇÊûúÊòØÂ§ö‰∏™Â≠êËäÇÁÇπÔºåÂàôÈÅçÂéÜÂπ∂Ë∞ÉÁî® mount ÂáΩÊï∞ÊåÇËΩΩ
      for (let i = 0; i < children.length; i++) {
        mount(children[i], el, isSVG);
      }
    }
  }
  // Â§ÑÁêÜsvgÊ†áÁ≠æ
  container.appendChild(el); //
}

// ÊåÇËΩΩÁªÑ‰ª∂
function mountComponent(vnode, container, isSVG) {
	if (vnode.flags & VNodeFlags.COMPONENT_STATEFUL) {
		mountStatefulComponent(vnode, container, isSVG)
	} else {
		mountFunctionalComponent(vnode, container, isSVG)
	}
}
// ÊåÇËΩΩÊúâÁä∂ÊÄÅÁªÑ‰ª∂
function mountStatefulComponent(vnode, container, isSVG) {
	// 1„ÄÅÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æã
	const instance = new vnode.tag
	// 2„ÄÅËé∑ÂèñÁªÑ‰ª∂‰∫ßÂá∫ÁöÑ Vnode
	instance.$vnode = instance.render()
	// 3„ÄÅmountÊåÇËΩΩ
	mount(instance.$vnode, container, isSVG)
	// 4„ÄÅËÆ©ÁªÑ‰ª∂ÂÆû‰æãÁöÑ $el Â±ûÊÄßÂíå vnode.el Â±ûÊÄßÁöÑÂÄºÂºïÁî®ÁªÑ‰ª∂ÁöÑÊ†πDOMÂÖÉÁ¥†
	instance.$el = vnode.el = instance.$vnode.el
}

// ÊåÇËΩΩÂáΩÊï∞ÂºèÁªÑ‰ª∂
function mountFunctionalComponent(vnode, container, isSVG) {
	// Ëé∑Âèñvnode
	const $vnode = vnode.tag()
	// ÊåÇËΩΩ
	mount($vnode, container, isSVG)
	// elÂÖÉÁ¥†ÂºïÁî®ËØ•ÁªÑ‰ª∂ÁöÑÊ†πÂÖÉÁ¥†
	vnode.el = $vnode.el
}

// ÊåÇËΩΩÁ∫ØÊñáÊú¨
function mountText(vnode, container){
	const el = document.createTextNode(vnode.children)
	vnode.el = el
	container.appendChild(el)
}

// ÊåÇËΩΩFragment
function mountFragment(vnode, container, isSVG) {
	// ÊãøÂà∞children Âíå childFlags
	const {children, childFlags} = vnode
	switch (childFlags) {
		case ChildrenFlags.SINGLE_VNODE:
			mount(children, container, isSVG)
			vnode.el = children.el
			break
		case ChildrenFlags.NO_CHILDREN:
			// Ê≤°ÊúâÂ≠êËäÇÁÇπÔºåÁ≠â‰ª∑‰∫éÊåÇËΩΩÁ©∫ÁâáÊÆµÔºå‰ºöÂàõÂª∫‰∏Ä‰∏™Á©∫ÁöÑÊñáÊú¨ËäÇÁÇπÂç†‰Ωç
			const placeholder = createTextVNode('')
			mountText(placeholder, container)
			vnode.el = placeholder.el
			break
		default:
			// Â§ö‰∏™Â≠êËäÇÁÇπÔºåÈÅçÂéÜÊåÇËΩΩ
			for (let i = 0; i < children.length; i++) {
				mount(children[i], container, isSVG)
			}
			vnode.el = children[0].el
			break
	}
}

// ÊåÇËΩΩ Portal
function mountPortal(vnode, container, isSVG) {
	const {tag, children, childFlags} = vnode
	// Ëé∑ÂèñÊåÇËΩΩÁÇπ
	const target = typeof tag === 'string' ? document.querySelector(tag) : tag

	if (childFlags & childFlags.SINGLE_VNODE) {
		// Â∞ÜchildrenÊåÇËΩΩÂà∞target‰∏äÔºåËÄåÈùûcontainer
		mount(children, target)
	} else if (childFlags & ChildrenFlags.MULTIPLE_VNODES) {
		for (let i = 0; i < children.length; i++) {
			mount(children[i], target)
		}
	}
	// Âç†‰ΩçÁöÑÁ©∫ÊñáÊú¨ËäÇÁÇπ
	const placeholder = createTextVNode('')
	// Â∞ÜËØ•ËäÇÁÇπÊåÇËΩΩÂà∞ container ‰∏≠
	mountText(placeholder, container, null)
	// elÂ±ûÊÄßÂºïÁî®ËØ•ËäÇÁÇπ
	vnode.el = placeholder.el

}

function dynamicClass(className) {
  if (typeof className === "string") {
    return className;
  }
  if (typeof className === "object") {
    if (Array.isArray(className)) {
      let str = "";
      for (let i = 0; i < className.length; i++) {
        if (typeof className[i] === "string") {
          str += className[i] + " ";
        } else if (typeof className[i] === "object") {
          str += dynamicClass(className[i]);
        }
      }
      return str || "";
    } else {
      let str = "";
      for (let key in className) {
        if (typeof className[key] === "boolean" && className[key]) {
          str += key + " ";
        } else if (typeof className[key] === "object") {
          str += dynamicClass(className[key]);
        }
      }
      return str || "";
    }
  }
  return "";
}

export default {
  render
};
